.popup {
	-webkit-overflow-scrolling: touch;
	position:                   fixed;
	top:                        0;
	left:                       0;
	width:                      100%;
	height:                     100%;
	padding:                    30px 10px;
	transition:                 visibility 0.8s ease 0s;
	visibility:                 hidden;
	z-index:                    100;

	&::before {
		position:         fixed;
		content:          '';
		top:              0;
		left:             0;
		width:            100%;
		height:           100%;
		background-color: rgba(0, 0, 0, 0.9);
		transition:       opacity 0.8s ease 0s;
		opacity:          0;
	}


	&.popup-quiz {
		.popup__body {
			max-width: 1200px;
		}
	}

	&.show {
		visibility: visible;
		overflow:   auto;

		&::before {
			opacity: 1;
		}

		.popup__body {
			transform: scale(1);
		}
	}

	&._active {
		overflow:   auto;
		visibility: visible;

		&::before {
			opacity: 1;
		}

		.popup__body {
			transition: all .3s ease .2s;
			transform:  scale(1);
		}
	}

	&__content {
		width:           100%;
		min-height:      100%;
		display:         flex;
		flex-direction:  column;
		align-items:     center;
		flex:            1 1 auto;
		display:         flex;
		justify-content: center;
		align-items:     center;
	}

	&__body {

		width:            100%;
		display:          flex;
		flex-direction:   column;
		border-radius:    5px;
		overflow:         hidden;
		background-color: #fff;
		//padding: 48px 85px 38px 85px;
		padding:          0;
		transform:        scale(0);
		transition:       all .3s ease 0s;
	}

	&__close {
		position:   absolute;
		top:        0;
		right:      0;
		width:      25px;
		height:     25px;
		cursor:     pointer;
		background: #0d95e8;
		z-index:    30;

		&:before, &:after {
			position:      absolute;
			content:       '';
			inset:         0;
			width:         20px;
			height:        2px;
			background:    #1E1E1E;
			margin-inline: auto;
			margin-block:  auto;
		}

		&:before {
			rotate: 45deg;
		}

		&:after {
			rotate: -45deg;
		}
	}
}

