.quiz {

	.quiz__body-inner {
		@media (max-width: 767px) {
			flex-direction: column;
			row-gap:        15px;
		}
	}

	&__item {
		position:        relative;
		display:         none;
		justify-content: center;

		&.active {
			display: flex;
		}

		&#quiz-1 {
			.quiz__footer {
				justify-content: flex-end;
			}
		}
	}

	&__item-left {
		width:           70%;
		display:         flex;
		flex-direction:  column;
		justify-content: flex-end;
		padding:         37px 58px 37px 80px;

		@media (max-width: 992px) {
			padding: 30px 25px;
		}

		@media (max-width: 767px) {
			width:   100%;
			padding: 20px 10px;
			z-index: 1;
		}
	}

	&__header {
		margin-bottom: 20px;

		h2 {
			font-size:   30px;
			@include aFont(30, 20, 1);
			font-weight: 700;
			color:       #000;
		}
	}

	&__inner {
		display:        flex;
		flex-direction: column;
		row-gap:        20px;
		margin-bottom:  30px;

		@media (max-width: 767px) {
			row-gap: 10px;
		}

		p {
			max-width:   560px;
			font-size:   13px;
			@include aFont(13, 10, 1);
			line-height: 23px;
			font-weight: 500;
			color:       #242424;
		}
	}

	&__check {
		margin-bottom: 80px;
	}

	&__footer {
		display:         flex;
		justify-content: space-between;
		align-items:     center;

		button {
			@extend .btn-next;
		}

		.btn-next {
			width:         max-content;
			display:       flex;
			align-items:   center;
			column-gap:    7px;
			font-size:     15px;
			@include aFont(15, 13, 1);
			font-weight:   700;
			border-radius: 5px;
			color:         #FFF;
			background:    #2A77E9;
			padding:       16.5px 22.5px 16.5px 26px;
		}
	}

	&__item-right {
		width: 30%;

		img {
			display:   block;
			max-width: 100%;
			width:     100%;
			height:    100%;

			@media (max-width: 767px) {
				opacity: .22;
			}
		}

		@media (max-width: 767px) {
			position:   absolute;
			inset:      0;
			width:      100%;
			height:     100%;
			background: rgba(0, 0, 0, .33);
			margin:     auto;
			z-index:    0;
		}
	}
}

