			flex-direction: row;
			column-gap:     5px;
		}
	}

	label {
		display:        flex;
		flex-direction: column;
		row-gap:        7px;
		font-size:      13px;
		@include aFont(13, 10, 1);
		font-weight:    500;
		color:          #000;
	}

	input {
		height:        44px;
		border-radius: 4px;
		border:        1px solid #E0E0E0;
		background:    #FAFAFA;
	}

	&__item {

		&_quiz-1 {
		}

		&_quiz-2 {
			.quiz__items {
				display:     flex;
				align-items: center;
				gap:         15px;

				@media (max-width: 767px) {
					gap: 10px;
				}

				label {
					input {
						transition: .4s ease 0s;

						&:focus {
							color:        #212529;
							border-color: #86b7fe;
							outline:      0;
							box-shadow:   0 0 0 0.25rem rgb(13 110 253 / 25%);
						}

						padding:    15px;
					}
				}
			}

			.quiz__items_two {
				label {
					width: 164px;

					input {
						width:   auto;
						padding: 15px;

						@media (max-width: 767px) {
							padding: 10px;
						}
					}
				}
			}
		}

		&_quiz-3 {
