							}

							input:checked ~ .checkmark {
								border-color: #2A77E9;
								background:   #2A77E9;
							}

							input {
								position: absolute;
								opacity:  0;
								cursor:   pointer;
								height:   0;
								width:    0;
								height:   auto;
							}

							.checkmark {
								position:      absolute;
								top:           0;
								bottom:        0;
								left:          0;
								height:        16px;
								width:         16px;
								border-radius: 1px;
