	&__footer {
		.btn-prev {
			display:     flex;
			align-items: center;
			column-gap:  10px;
			font-size:   15px;
			@include aFont(15, 13, 1);
			font-weight: 700;
			color:       #C9C9C9;
			transition:  .4s ease 0s;

			svg {
				path {
					transition: .4s ease 0s;
				}
			}

			@media (any-hover: hover) {
				&:hover {
					color: #2A77E9;

					svg {
						path {
							fill: #2A77E9;
						}
					}
				}
			}
		}

		.btn-next {
			span {
				transition: .4s ease 0s;
			}

			svg {
				path {
					transition: .4s ease 0s;
				}
			}

			@media (any-hover: hover) {
				&:hover {
					background: white;

					span {
						color: #2A77E9;
					}

					svg {
						path {
							fill: #2A77E9;
						}
					}
				}
			}
		}
	}

	&__body {
		display:        flex;
		flex-direction: column;
		gap:            15px;
		margin-bottom:  12px;

		@media (max-width: 767px) {
			gap: 10px;
		}
	}

	&__items {
		@media (max-width: 992px) {
			//flex-direction: column;
			flex-wrap: wrap;
		}
	}

	&__slot-title {
		margin-bottom: 7px;

		p {
			font-size:   13px;
			@include aFont(13, 10, 1);
			font-weight: 500;
			color:       #000;
